import { variant } from "./variant";
import { v4 as uuidv4 } from "uuid";


export const PROJECT_SCHEMA_VERSION = '0.2.0';
export interface Project {
    title: string;
    description?: string;
    resumes: Resume[];
}
export const DEFAULT_USER_RESUME: () => variant<"user", UserDefinedResume> = () => ({
    type: "user",
    user: {
        id: uuidv4(),
        lastEditedAt: Date.now(),
        title: "Initial",
        resume: "",
        roleDescription: "",
    }
})
export const DEFAULT_PROJECT: () => Project = () => ({
    title: "Resume Advisor",
    resumes: [DEFAULT_USER_RESUME()]
})

export type Resume = variant<"user", UserDefinedResume> | variant<"advisor", AdvisorDefinedResume>;

export function getBaseResume(resume: Resume): BaseResume {
    if (resume.type === "advisor") {
        return resume.advisor;
    }
    return resume.user;
}
export function overwriteBaseResume(resume: Resume, baseResume: Partial<BaseResume>): Resume {
    switch (resume.type) {
        case "advisor":
            return {
                type: "advisor",
                advisor: {
                    ...resume.advisor,
                    ...baseResume,
                }
            }
        case "user":
            return {
                type: "user",
                user: {
                    ...resume.user,
                    ...baseResume,
                }
            }
    }
}

interface BaseResume {
    id: string;
    title: string;
    parent?: string;
    resume: string;
    roleDescription: string;
    lastEditedAt: number;
}

export interface UserDefinedResume extends BaseResume {}

export interface AdvisorDefinedResume extends BaseResume {
    prompt: string;
    suggestionSummary: string;
}
