import * as React from "react";
import { Resume, getBaseResume } from "../types/project";
import { Flex, Timeline, Text, Group, ActionIcon } from "@mantine/core";
import { IconAB, IconBrandOpenai, IconUserEdit } from "@tabler/icons-react";
import { IconX } from "@tabler/icons-react";


interface ResumeItemProps {
    resume: Resume;
    allResumeItems: Resume[];
    isActive?: boolean;
    onSetActive?: () => void;
    isComparisonBase?: boolean;
    onSetAsComparisonBase?: () => void;
    onRemove?: () => void;
}
export const ResumeItem: React.FC<ResumeItemProps> = props => {
    const baseResume = getBaseResume(props.resume);
    const bullet = props.resume.type === "user" ? <IconUserEdit size={12} /> : <IconBrandOpenai size={12} />;
    const filledIf = (condition?: boolean) => condition ? "filled" : "subtle";
    const boldIfActive = props.isActive ? { fontWeight: 600 } : {};
    const stopPropagation = React.useCallback((callback?: () => void) =>
        (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
            callback?.();
            event.stopPropagation();
        }, []);
    const title = (
        <Flex direction="row" gap="sm">
            <Text style={{...boldIfActive, flex: "0 1 auto"}} truncate>{baseResume.title}</Text>
            <ActionIcon variant={filledIf(props.isComparisonBase)} aria-label="Compare" color="grey" size="sm" onClick={stopPropagation(props.onSetAsComparisonBase)}>
                <IconAB style={{ width: '80%', height: '80%' }} stroke={1.5} />
            </ActionIcon>
            <ActionIcon variant="subtle" aria-label="Remove" color="grey" size="sm" onClick={stopPropagation(props.onRemove)}>
                <IconX style={{ width: '80%', height: '80%' }} stroke={1.5} />
            </ActionIcon>
        </Flex>
    );
    return (
        <Timeline.Item bullet={bullet} title={title} onClick={props.onSetActive}>
            <Group>
                <Text c="dimmed" size="sm">{(new Date(baseResume.lastEditedAt)).toLocaleString()}</Text>
            </Group>
        </Timeline.Item>
    );
}
