import { AppShell, Modal } from '@mantine/core';
import { Header } from './components/Header';
import { SettingsEditor } from './components/SettingsEditor';
import { useDisclosure, useLocalStorage } from '@mantine/hooks';
import { DEFAULT_PROJECT, PROJECT_SCHEMA_VERSION, Project } from "./types/project";

import { ProjectEditor } from "./components/ProjectEditor";
import { useSettings } from "./hooks/useSettings";


export function App() {
    const [openedSettingsModal, { open: openSettingsModal, close: closeSettingsModal }] = useDisclosure(false);
    const {settings, setSettings} = useSettings();
    const [project, setProject] = useLocalStorage<Project>({
        key: `resume-advisor-project/v/${PROJECT_SCHEMA_VERSION}`,
        defaultValue: DEFAULT_PROJECT(),
    });
    return (
        <AppShell
            header={{ height: 60 }}
            padding="md"
            
        >
            <AppShell.Header>
                <Header onClickSettings={openSettingsModal} />
            </AppShell.Header>
            <AppShell.Main>
                <Modal opened={openedSettingsModal} onClose={closeSettingsModal} title="LLM Settings">
                    <SettingsEditor settings={settings} setSettings={setSettings} onSaved={() => closeSettingsModal()} />
                </Modal>
                <ProjectEditor project={project} onChangeProject={setProject} />
            </AppShell.Main>
        </AppShell>
    );
}
