import * as React from "react";
import { DEFAULT_USER_RESUME, Resume, getBaseResume, overwriteBaseResume } from "../types/project";
import { Timeline, Button, Stack } from "@mantine/core";
import { ResumeItem } from "./ResumeItem";
import { IconCirclePlus } from "@tabler/icons-react";


interface ResumeListProps {
    resumes: Resume[];
    activeResumeIndex: number;
    setActiveResumeIndex: (index: number) => void;
    compareResumeIndex?: number;
    setCompareResumeIndex: (indexOrNone: number | undefined) => void;
    onAddResume: (resume: Resume) => void;
    onRemoveResumeAtIndex: (index: number) => void;
}
export const ResumeList: React.FC<ResumeListProps> = props => {
    const { resumes, activeResumeIndex, setActiveResumeIndex, compareResumeIndex,
        setCompareResumeIndex, onAddResume, onRemoveResumeAtIndex: onRemoveResume } = props;

    const onSetActiveResumeIndex = React.useCallback((index: number) => () =>
        setActiveResumeIndex(index), [setActiveResumeIndex]);
    const onToggleComparisonBaseAtIndex = React.useCallback((index: number) => () =>
        setCompareResumeIndex(index === compareResumeIndex ? undefined : index),
        [compareResumeIndex, setCompareResumeIndex])
    const onRemoveResumeAtIndex = React.useCallback((index: number) => () =>
        onRemoveResume(index), [onRemoveResume]);
    const maybeCurrentResume: Resume | undefined = resumes[activeResumeIndex];
    const onAddEmptyResumeOrCopyCurrent = React.useCallback(() => {
        const maybeCopiedUserResume: Resume | undefined = maybeCurrentResume !== undefined
            ? maybeCurrentResume.type === "advisor"
                // convert advisor to user
                ? {type: "user", user: maybeCurrentResume.advisor}
                : maybeCurrentResume
            : undefined;
        const newResume: Resume = maybeCopiedUserResume !== undefined
            ? overwriteBaseResume(maybeCopiedUserResume, { lastEditedAt: Date.now() })
            : undefined
            || DEFAULT_USER_RESUME();
        onAddResume(newResume);
    }, [onAddResume, maybeCurrentResume]);
    return (
        <Stack>
            <Timeline bulletSize={24} lineWidth={1.5}>
                {resumes.map((resume, index) =>
                    <ResumeItem
                        key={getBaseResume(resume).id}
                        resume={resume}
                        allResumeItems={resumes}
                        isActive={index === activeResumeIndex}
                        onSetActive={onSetActiveResumeIndex(index)}
                        isComparisonBase={index === compareResumeIndex}
                        onSetAsComparisonBase={onToggleComparisonBaseAtIndex(index)}
                        onRemove={onRemoveResumeAtIndex(index)}
                    />
                )}
            </Timeline>
            <Button variant="outline" leftSection={<IconCirclePlus size={14} />} onClick={onAddEmptyResumeOrCopyCurrent}>
                {maybeCurrentResume !== undefined ? "New from current" : "New"}
            </Button>
        </Stack>
    )
}
