import { Settings } from "../hooks/useSettings";
import { Resume, getBaseResume } from "../types/project";
import { variant } from "../types/variant";
import OpenAI from 'openai';


const SYSTEM_PROMPT = `You are an expert system to adapt user resumes to a role descriptions.

Suggest changes to the resume based on the role description. Do not deviate too much from users original resume, but you can add some detail to unspecific sentences, rephrase passages or leave entire parts out that are not relevant.

First, respond with thoughts about how the users resume can be tailored to the role description.
Second, write === followed by the improved resume based on all suggested changes.
`;


export type ResumeChanges = {
    suggestionsForChanges: string;
    newResume: string;
    prompt: string;
}
type ResumeChangesOrError = variant<"resumeChanges", ResumeChanges> | variant<"error", string>;
export async function getResumeChanges(settings: Settings, currentResume: Resume): Promise<ResumeChangesOrError> {
    const { resume, roleDescription } = getBaseResume(currentResume);
    const prompt = settings.prompt.replace('{roleDescription}', roleDescription).replace('{resume}', resume);
    const openai = new OpenAI({
        apiKey: settings.apiKey,
        dangerouslyAllowBrowser: true,
    });
      
    const chatCompletion = await openai.chat.completions.create({
        messages: [
            { role: "system", content: SYSTEM_PROMPT },
            { role: "user", content: prompt }
        ],
        model: 'gpt-3.5-turbo',
        temperature: 0.1,
    });

    const completion = chatCompletion.choices[0].message.content;
    if (completion == null) {
        return {
            type: "error",
            error: "CV Aid did not response",
        }
    }
    const [suggestions, newResume] = completion.split('===');
    if ((suggestions == null) || (newResume == null)) {
        return {
            type: "error",
            error: "CV Aid could not generate a resume",
        }
    }

    return {
        "type": "resumeChanges",
        "resumeChanges": {
            suggestionsForChanges: suggestions.trim(),
            newResume: newResume.trim(),
            prompt,
        }
    }
}
