import * as React from "react";
import { AdvisorDefinedResume, Project, Resume, UserDefinedResume, getBaseResume } from "../types/project";
import { Flex, Grid, Button, Tooltip, Loader, Alert, Text, Space } from "@mantine/core";
import { notifications } from '@mantine/notifications';
import ReactDiffViewer from 'react-diff-viewer'
import { useSettings } from "../hooks/useSettings";
import { v4 as uuidv4 } from "uuid";
import { ResumeChanges, getResumeChanges } from "../services/getResumeChanges";
import { useSetProperty } from "../hooks/useSetProperty";
import { UserResumeEditor } from "./UserResumeEditor";
import { ResumeList } from "./ResumeList";
import { IconInfoCircle } from "@tabler/icons-react";


interface ProjectProjectEditorProps {
    project: Project;
    onChangeProject: (newProject: Project) => void;
}
export const ProjectEditor: React.FC<ProjectProjectEditorProps> = props => {
    const { settings } = useSettings();
    const [activeResumeIndex, setActiveResumeIndex] = React.useState<number>(props.project.resumes.length - 1);
    const [compareResumeIndex, setCompareResumeIndex] = React.useState<number | undefined>(undefined);
    const resume: Resume | undefined = props.project.resumes[activeResumeIndex];
    const compareResume: Resume | undefined = compareResumeIndex !== undefined ? props.project.resumes[compareResumeIndex] : undefined;

    const onChangeResumes = useSetProperty("resumes", props.project, props.onChangeProject);

    const onAddResume = React.useCallback((newResume: Resume) => {
        const newResumeList = [...props.project.resumes, newResume];
        onChangeResumes(newResumeList);
        setActiveResumeIndex(newResumeList.length - 1);
    }, [onChangeResumes, setActiveResumeIndex, props.project.resumes]);

    const onRemoveResumeAtIndex = React.useCallback((index: number) => {
        onChangeResumes(props.project.resumes.filter((_, i) => i !== index))
        if (activeResumeIndex >= index) {
            setActiveResumeIndex(activeResumeIndex - 1);
        }
    }, [onChangeResumes, setActiveResumeIndex, activeResumeIndex, props.project.resumes]);

    const onChangeResume = React.useCallback((newUserResume: UserDefinedResume) => {
        const currentResume = props.project.resumes[activeResumeIndex];
        const newResume: Resume = { type: "user", user: newUserResume };
        switch (currentResume.type) {
            case "advisor":
                // do not edit the advisor resume but instead create a new user defined resume
                onAddResume(newResume);
                break;
            case "user":
                // edit the user resume in place
                onChangeResumes(props.project.resumes.map((r, i) => i === activeResumeIndex ? newResume : r))
                break;
        }
    }, [activeResumeIndex, props.project.resumes, onAddResume, onChangeResumes]);

    const [isLoadingSuggestion, setIsLoadingSuggestion] = React.useState<boolean>(false);
    const onGetSuggestion = React.useCallback(async () => {
        setIsLoadingSuggestion(true);
        const resumeChanges = await getResumeChanges(settings, resume);
        if (resumeChanges.type === "error") {
            notifications.show({
                title: "Error",
                message: resumeChanges.error,
                color: "red",
            });
        } else {
            setCompareResumeIndex(activeResumeIndex);
            onAddResume({
                type: "advisor",
                advisor: getAdvisorResume(resume, resumeChanges.resumeChanges),
            });
        }
        setIsLoadingSuggestion(false);
    }, [setIsLoadingSuggestion, settings, resume, onAddResume, activeResumeIndex]);

    return (
        <Grid>
            <Grid.Col span={3}>
                <Text fw={500} size="sm">Versions</Text>
                <Space h="xs" />
                <ResumeList
                    resumes={props.project.resumes}
                    activeResumeIndex={activeResumeIndex}
                    setActiveResumeIndex={setActiveResumeIndex}
                    compareResumeIndex={compareResumeIndex}
                    setCompareResumeIndex={setCompareResumeIndex}
                    onRemoveResumeAtIndex={onRemoveResumeAtIndex}
                    onAddResume={onAddResume}
                />
            </Grid.Col>
            <Grid.Col span={9}>
                <Flex direction="column" gap="md">
                    {resume === undefined
                        ? null
                        : resume.type === "advisor" ? (<>
                            <Alert variant="light" color="blue" title="Advice" icon={<IconInfoCircle />}>
                                {resume.advisor.suggestionSummary}
                            </Alert>
                            <UserResumeEditor resume={resume.advisor} />
                        </>) : (<>
                            <UserResumeEditor resume={resume.user} onChangeResume={onChangeResume} />
                            <Tooltip label="Add API key to settings" hidden={settings.apiKey != null}>
                                <Button
                                    fullWidth={false}
                                    variant="outline"
                                    onClick={onGetSuggestion}
                                    disabled={isLoadingSuggestion || settings.apiKey == null}
                                    rightSection={isLoadingSuggestion ? <Loader color="white" size={12} /> : null}
                                >
                                    Get Suggestion
                                </Button>
                            </Tooltip>
                        </>)
                    }
                    {resume !== undefined && compareResume !== undefined && (
                        <ReactDiffViewer
                            newValue={getBaseResume(resume).resume}
                            oldValue={getBaseResume(compareResume).resume}
                            splitView={true}
                            styles={{ diffContainer: { fontSize: "11px" } }}
                        />
                    )}
                </Flex>
            </Grid.Col>
        </Grid>
    )
}

function getAdvisorResume(resume: Resume, changes: ResumeChanges): AdvisorDefinedResume {
    return {
        id: uuidv4(),
        parent: getBaseResume(resume).id,
        lastEditedAt: Date.now(),
        roleDescription: getBaseResume(resume).roleDescription,
        title: getBaseResume(resume).title,
        resume: changes.newResume,
        prompt: changes.prompt,
        suggestionSummary: changes.suggestionsForChanges,
    };
}

