import { Button, Flex, Textarea } from "@mantine/core";
import * as React from "react";
import { Settings } from "../hooks/useSettings";


interface SettingsEditorProps {
    settings: Settings;
    setSettings: (newSettings: Settings) => void;
    onSaved?: () => void;
}
export const SettingsEditor: React.FC<SettingsEditorProps> = props => {
    const {settings, onSaved, setSettings} = props;
    const [tempSettings, setTempSettings] = React.useState<Settings>(settings);
    const onChange = React.useCallback((key: keyof Settings) => (event: React.ChangeEvent<HTMLTextAreaElement>) =>
    setTempSettings({...tempSettings, [key]: event.currentTarget.value}), [tempSettings, setTempSettings]);
    
    const saveSettings = React.useCallback(() => {
        setSettings({...tempSettings});
        if (onSaved != null) {
            onSaved();
        }
    }, [setSettings, tempSettings, onSaved]);
    
    return (
        <Flex direction="column" gap="md">
            <Textarea
                label="Prompt"
                description="Instructions for the LLM to assist with your CV"
                value={tempSettings.prompt}
                onChange={onChange("prompt")}
                autosize
                maxRows={12}
            />
            <Textarea
                label="OpenAI API Key"
                description="API key for OpenAI"
                value={tempSettings.apiKey}
                onChange={onChange("apiKey")}
            />
            <Button variant="filled" fullWidth onClick={saveSettings}>Save</Button>
        </Flex>
    )
}
