import * as React from "react";

export function useSetProperty<O extends object, K extends keyof O>(
    key: K, obj: O, setter: (obj: O) => void): (value: O[K]) => void {
    return React.useCallback((value: O[K]) => setter({ ...obj, [key]: value }), [key, obj, setter]);
}

export function useSetPropertyWithExtractor<O extends object, K extends keyof O, T>(
    key: K, obj: O, setter: (obj: O) => void, extract: (value: T) => O[K]): (value: T) => void {
    return React.useCallback((value: T) => setter({ ...obj, [key]: extract(value) }), [key, obj, setter, extract]);
}
