import * as React from "react";
import { UserDefinedResume } from "../types/project";
import { Grid, Textarea, TextInput } from "@mantine/core";
import { useSetPropertyWithExtractor } from "../hooks/useSetProperty";


interface UserResumeEditorProps {
    resume: UserDefinedResume;
    onChangeResume?: (newResume: UserDefinedResume) => void;
}
export const UserResumeEditor: React.FC<UserResumeEditorProps> = props => {
    const {onChangeResume} = props;
    const maybeChangeResume: (newResume: UserDefinedResume) => void = React.useCallback(
        resume => { if (onChangeResume !== undefined) { onChangeResume(resume) } },
        [onChangeResume]
    );
    const isNotEditable = props.onChangeResume === undefined;
    return (
        <Grid>
            <Grid.Col span={12}>
                <TextInput
                    disabled={isNotEditable}
                    label="Title"
                    value={props.resume.title}
                    onChange={useSetPropertyWithExtractor("title", props.resume, maybeChangeResume, e => e.currentTarget.value)}
                />
            </Grid.Col>
            <Grid.Col span={6}>
                <Textarea
                    disabled={isNotEditable}
                    autosize
                    label="Resume"
                    value={props.resume.resume}
                    onChange={useSetPropertyWithExtractor("resume", props.resume, maybeChangeResume, e => e.currentTarget.value)}
                    maxRows={12}
                />
            </Grid.Col>
            <Grid.Col span={6}>
                <Textarea
                    disabled={isNotEditable}
                    autosize
                    label="Role description"
                    value={props.resume.roleDescription}
                    onChange={useSetPropertyWithExtractor("roleDescription", props.resume, maybeChangeResume, e => e.currentTarget.value)}
                    maxRows={12}
                />
            </Grid.Col>
        </Grid>
    );
}